<template>
  <div>
    <!-- -->
    <van-nav-bar title="馆内音乐" right-text="点歌" @click-right="wdsfshow = true">
      <template #left> 传奇宇宙健身店 </template>
    </van-nav-bar>
    <van-notice-bar left-icon="volume-o" :scrollable="false">
      <van-swipe vertical class="notice-swipe" :autoplay="2000" :show-indicators="false">
        <van-swipe-item>馆内正在播放：《XXXX》</van-swipe-item>
        <van-swipe-item>没有想听的歌曲，请及时反馈</van-swipe-item>
        <van-swipe-item>下次来一定有你想要的歌曲</van-swipe-item>
      </van-swipe>
    </van-notice-bar>
    <div class="sskbox">
      <van-search
        background="#D4461A"
        show-action
        label="歌名/歌手"
        placeholder="请输入歌名/歌手关键词"
      >
        <template #action>
          <div @click="wdsfshow = true">搜索</div>
        </template>
      </van-search>
    </div>
    <div class="rq" @click="ockdas"></div>
    <div style="margin-top: 80px">
      <!-- 准备一个容器用来存放音乐播放器 -->
      <div id="aplayer"></div>
    </div>

    <van-popup
      closeable
      close-icon="close"
      v-model="wdsfshow"
      round
      position="right"
      :style="{ height: '90%', width: '90%' }"
    >
      <div style="text-align: center; margin: 20px">
        <span style="float: left">反馈</span> 音乐列表
      </div>
      <el-card class="box-card">
        <div v-for="o in 4" :key="o" class="text item">
          {{ "列表内容 " + o }}
        </div>
      </el-card>

      如果没有找到想听的音乐请及时反馈。
    </van-popup>
  </div>
</template>

<script>
import "aplayer/dist/APlayer.min.css";
import APlayer from "aplayer";
import { Notify } from "vant";
const a = require("@/assets/mp3/1.mp3");
const b = require("@/assets/mp3/2.mp3");
export default {
  name: "LufsVueDJ",

  data() {
    return {
      wdsfshow: false,
      audio: [
        // 歌曲列表
        {
          name: "《一吻天荒-无损》", // 歌曲名字
          artist: "钢琴-羽肿", // 歌曲演唱者
          // 歌曲地址（这里用外链地址）
          url: a,
          cover: "http://imge.kugou.com/stdmusic/150/20170815/20170815070007812976.jpg", // 歌曲头像
          lrc: "", // 歌词
          theme: "rgb(127, 218, 180)", // 播放这首歌曲时的主题色
        },
        {
          name: "《樱花树下的约定》",
          artist: "古筝-付娜",
          url: b,
          cover: "http://imge.kugou.com/stdmusic/150/20200606/20200606220631519630.jpg",
          lrc: "",
          theme: "rgb(61, 162, 230)",
        },
      ],
      info: {
        fixed: false, // 不开启吸底模式
        listFolded: false, // 折叠歌曲列表
        autoplay: true, // 开启自动播放
        preload: "auto", // 自动预加载歌曲
        loop: "all", // 播放循环模式、all全部循环 one单曲循环 none只播放一次
        order: "list", //  播放模式，list列表播放, random随机播放
      },
      ap: null, // 用于存储 APlayer 实例
      currentSong: null, // 用于存储当前正在播放的歌曲信息
    };
  },

  mounted() {
    // 初始化播放器
    this.initAudio();
  },

  methods: {
    ockdas() {
      Notify({ type: "primary", message: "仅供预览，如需切换馆歌曲请点歌" });
      console.log(`正在播放`, this.currentSong);
      // 切换到指定下标歌曲
      // this.ap.list.switch(2);
    },
    initAudio() {
      // 创建一个音乐播放器实例，并挂载到DOM上，同时进行相关配置
      this.ap = new APlayer({
        container: document.getElementById("aplayer"),
        audio: this.audio, // 音乐信息
        ...this.info, // 其他配置信息
      });
      // 监听 APlayer 的 play 事件
      this.ap.on("play", () => {
        // 获取当前播放的歌曲信息
        this.currentSong = this.ap.list;
        console.log(`歌曲切换/播放`, this.ap.list);
      });
    },
  },
};
</script>

<style scoped>
.notice-swipe {
  height: 40px;
  line-height: 40px;
}
.sskbox {
  position: absolute;
  z-index: 990;
  width: 100%;
}
.rq {
  position: absolute;
  z-index: 99;
  background-color: rgba(210, 210, 210, 0.175);
  width: 100%;
  height: 80%;
  font-size: 20px;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  justify-items: center;
  align-content: center;
}
.rq > span {
  background-color: #56b2f4;
  box-shadow: #70b3e3 1px 1px 10px;
}
</style>
