        <van-tag type="danger">拥挤</van-tag>
      </template>
    </van-nav-bar>

    <el-drawer :visible.sync="drawer" :direction="direction" size="100%">
      <template #title>
        <div @click="xzcssd = true">选择门店 <van-icon name="location" />上海</div>
      </template>
      <div class="xzcs" v-if="xzcssd">
        <div style="width: 100%">
          <van-area
            @confirm="xzcssd = false"
            @cancel="xzcssd = false"
            title="选择所在城市"
            :area-list="areaList"
            :columns-num="2"
          />
        </div>
      </div>

      <div class="medlist">
        <div>正在加盟中</div>

        <div>正在加盟中</div>

        <div>正在加盟中</div>

        <div>正在加盟中</div>
      </div>
    </el-drawer>

    <van-swipe :autoplay="1000">
      <van-swipe-item>
        <img class="swimmg" src="@/assets/img/1.png" alt="" srcset="" />
      </van-swipe-item>
      <van-swipe-item>
        <img class="swimmg" src="@/assets/img/2.png" alt="" srcset=""
      /></van-swipe-item>
      <van-swipe-item>
        <img class="swimmg" src="@/assets/img/3.png" alt="" srcset=""
      /></van-swipe-item>
      <van-swipe-item>
        <img class="swimmg" src="@/assets/img/4.png" alt="" srcset=""
      /></van-swipe-item>
    </van-swipe>

    <van-notice-bar
      color="#1989fa"
      background="#FFFFFF"
      left-icon="info-o"
      :scrollable="false"
    >
      <van-swipe vertical class="notice-swipe" :autoplay="1000" :show-indicators="false">
        <van-swipe-item>本馆提供服务如下</van-swipe-item>
        <van-swipe-item>本馆当前音乐正在播放《凤凰传奇》</van-swipe-item>
        <van-swipe-item>本馆禁止吸烟、爱护环境人人有责</van-swipe-item>
      </van-swipe>
    </van-notice-bar>

    <van-grid :column-num="3">
      <van-grid-item icon="music" text="点歌" to="/DJ" />
      <van-grid-item icon="cart-circle" text="自助贩卖机" @click="fmj" />
      <van-grid-item icon="browsing-history" text="馆内监控" @click="gnjk" />
      <van-grid-item icon="volume" text="丢失登记" @click="opurlpaos(`/diusdji`)" />
      <van-grid-item icon="cluster" text="馆内设施" to="/guaniess" />
      <van-grid-item icon="description" text="器材科普" to="/qickep" />
    </van-grid>

