</template>

<script>
import { Toast } from "vant";
import { areaList } from "@vant/area-data";
export default {
  name: "LufsVueIndex",

  data() {
    return {
      //是否显示选择城市
      xzcssd: false,
      drawer: false,
      direction: "ltr",
      areaList,
    };
  },

  mounted() {},

  methods: {
    fmj() {
      const toast = Toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "打开小程序\n 3 秒",
      });

      let second = 3;
      const timer = setInterval(() => {
        second--;
        if (second) {
          toast.message = `打开小程序\n ${second} 秒`;
        } else {
          clearInterval(timer);
          // 手动清除 Toast
          Toast.clear();
        }
      }, 1000);
    },
    gnjk() {
      Toast.fail("年费客户可查看");
    },
    opurlpaos(url) {
      this.$router.push(url);
    },
  },
};
</script>

