<template>
  <div>
    <van-nav-bar right-text="退出登录" @click-right="outlogin()">
      <template #title> 个人中心 </template>
    </van-nav-bar>

    <el-alert title="开通VIP享受智慧健身" type="warning"></el-alert>
    <div class="dbsab" @click="onkp">
      <div class="kapian">
        <span class="asldaji">累计健身：5天</span>
        <div class="sade">
          <el-avatar
            :size="55"
            src="https://tse3-mm.cn.bing.net/th/id/OIP-C.i3-pYOwDcwIGVRJk2Fxf8gHaG7?rs=1&pid=ImgDetMain"
          ></el-avatar>
          <span class="sadaxs">SEWASD</span>
        </div>
        <span v-if="isvip != `true`" class="ann" @click="showList = true"
          ><van-button size="small" color="#D74B0A" plain>立即开通</van-button></span
        >
        <div class="jbsdht">
          <el-progress
            :percentage="10"
            color="#DB4218"
            :show-text="false"
            :stroke-width="2"
          ></el-progress>
        </div>
        <div class="jisajdmx">
          LV1 健身萌新 <span style="font-size: 10px">10/500</span>
        </div>
        <div class="bianho">
          NO.00011 <span v-if="isvip != `false`">有效期至2019-02-09</span>
        </div>
      </div>
    </div>

    <div class="stisju" @click="GRXXshow = true">
      <div class="stjs">
        <span class="stis">身体数据</span>
        <span class="jaint"><van-icon name="arrow" /></span>
        <el-alert title="身高：180" type="success" show-icon :closable="false">
        </el-alert>
        <el-alert title="肌肉：10% 肌肉弱" type="warning" show-icon :closable="false">
        </el-alert>
        <el-alert title="体重：180 严重超标" type="error" show-icon :closable="false">
        </el-alert>
      </div>
    </div>

    <div class="sadasf">
      <div class="sanscy">
        常用功能
        <van-grid :column-num="3">
          <van-grid-item icon="award" text="我的课程" @click="wdkcshow = true" />
          <van-grid-item icon="invitation" text="我的预约" @click="wdyyshow = true" />
          <van-grid-item icon="notes" text="消费记录" @click="wdsfshow = true" />
        </van-grid>
      </div>

      <div class="sanscy">
        其他服务
        <van-grid :column-num="3">
          <van-grid-item icon="chart-trending-o" text="成长体系" @click="cztx" />
          <van-grid-item icon="service" text="联系客服" @click="collkgf" />
          <van-grid-item icon="miniprogram-o" text="加入我们" @click="OPENJRWM" />
        </van-grid>
      </div>
    </div>

    <!-- Coupon 选择器 组件 -->
    <van-popup
      v-model="showList"
      round
      position="bottom"
      style="height: 90%; padding-top: 4px"
    >
      <van-coupon-list
        :coupons="coupons"
        :chosen-coupon="chosenCoupon"
        :disabled-coupons="disabledCoupons"
        @change="onChange"
        @exchange="onExchange"
        :show-close-button="false"
        exchange-button-text="兑换VIP"
        input-placeholder="请输入兑换码"
        enabled-title="选择开通时长"
        disabled-title="优惠卷"
        :show-count="false"
      />
    </van-popup>

    <!-- 个人信息 -->
    <van-popup
      closeable
      close-icon="close"
      v-model="GRXXshow"
      round
      position="top"
      :style="{ height: '90%' }"
    >
      <div style="text-align: center; margin: 20px">编辑个人信息</div>
      <div style="text-align: center">
        <el-avatar
          :size="55"
          src="https://tse3-mm.cn.bing.net/th/id/OIP-C.i3-pYOwDcwIGVRJk2Fxf8gHaG7?rs=1&pid=ImgDetMain"
        >
        </el-avatar>
        <div class="formls">
          <van-cell-group>
            <van-field label="昵称" placeholder="请输入姓名" />
            <van-field label="身高" placeholder="请输入身高">
              <template #button> 厘米 </template>
            </van-field>
            <van-field label="体重" placeholder="请输入体重">
              <template #button> 斤 </template>
            </van-field>
            <van-field label="视力" placeholder="请输入视力">
              <template #button> 度 </template>
            </van-field>
          </van-cell-group>
        </div>
        <van-button
          style="background: linear-gradient(to right, #000000, #a4a3a3); border: none"
          round
          block
          hairline
          type="info"
          >保存</van-button
        >
      </div>
    </van-popup>

    <!-- 我的课程 -->
    <van-popup
      closeable
      close-icon="close"
      v-model="wdkcshow"
      round
      position="bottom"
      :style="{ height: '90%' }"
    >
      <div style="text-align: center; margin: 20px">已购课程</div>
      <van-card
        num="2"
        price="2.00"
        desc="描述信息"
        title="商品标题"
        thumb="https://img01.yzcdn.cn/vant/ipad.jpeg"
      >
        <template #tags>
          <van-tag plain type="danger">标签</van-tag>
          <van-tag plain type="danger">标签</van-tag>
        </template>
        <template #footer>
          <van-button size="mini">按钮</van-button>
          <van-button size="mini">按钮</van-button>
        </template>
      </van-card>
    </van-popup>

    <!-- 我的预约 -->
    <van-popup
      closeable
      close-icon="close"
      v-model="wdyyshow"
      round
      position="left"
      :style="{ height: '90%', width: '90%' }"
    >
      <div style="text-align: center; margin: 20px">我的预约</div>

      <van-calendar
        type="single"
        title="|---------------------------------------------------|"
        :poppable="false"
        :show-confirm="false"
        :style="{ height: '500px' }"
        :formatter="formatter"
      />
    </van-popup>

    <!-- 消费记录 -->
    <van-popup
      closeable
      close-icon="close"
      v-model="wdsfshow"
      round
      position="right"
      :style="{ height: '90%', width: '90%' }"
    >
      <div style="text-align: center; margin: 20px">我的消费</div>
      <el-card class="box-card">
        <div v-for="o in 4" :key="o" class="text item">
          {{ "列表内容 " + o }}
        </div>
      </el-card>
    </van-popup>
  </div>
</template>

<script>
import { Dialog } from "vant";
import { Toast } from "vant";
const coupon1 = {
  id: 1,
  condition: "VIP畅通无阻\n支持全门店",
  name: "开通1个月",
  //开始时间
  // startAt: 1489104000,
  //结束时间
  // endAt: 1514592000,
  customValidPeriod: "30天VIP特权",
  valueDesc: "250",
  unitDesc: "元",
};

const coupon2 = {
  id: 2,
  condition: "VIP畅通无阻\n支持全门店",
  name: "开通3个月",
  //开始时间
  // startAt: 1489104000,
  //结束时间
  // endAt: 1514592000,
  customValidPeriod: "90天VIP特权",
  valueDesc: "620",
  unitDesc: "元",
};

const coupon3 = {
  id: 3,
  condition: "VIP畅通无阻\n支持全门店",
  name: "开通12个月",
  //开始时间
  // startAt: 1489104000,
  //结束时间
  // endAt: 1514592000,
  customValidPeriod: "360天VIP特权，享受无上权益",
  valueDesc: "2000",
  unitDesc: "元",
};
import { Notify } from "vant";
export default {
  name: "LufsVuesvip",

  data() {
    return {
      formattertrue: true,
      wdsfshow: false,
      wdyyshow: false,
      wdkcshow: false,
      GRXXshow: false,
      isvip: false,
      //展示VIP卡套餐列表
      showList: false,
      //当前选中套餐的索引 -1为无默认
      chosenCoupon: -1,
      //可开通VIP套餐列表
      coupons: [coupon1, coupon2, coupon3],
      //不可开通VIP套餐列表
      disabledCoupons: [],
    };
  },

  mounted() {
    this.isvip = localStorage.getItem("isvip");
    console.log(`是否是VIP`, this.isvip);
  },
  beforeDestroy() {},
  methods: {
    cztx() {
      this.$router.push("/cztx");
    },
    formatter(day) {
      const month = day.date.getMonth() + 1;
      const date = day.date.getDate();

      if (month === 5) {
        if (date === 18) {
          day.topInfo = "有预约";
        }
        if (date === 20) {
          day.topInfo = "有预约";
        }
      }
      return day;
    },
    OPENJRWM() {
      this.$router.push("/JRWM");
    },
    collkgf() {
      Dialog.confirm({
        title: "客服",
        message: "当前门店暂无电话",
        confirmButtonText: "拨打",
      })
        .then(() => {
          // on confirm
        })
        .catch(() => {
          // on cancel
        });
    },
    onkp() {
      Toast({
        message: "等你好久啦~",
        icon: "like-o",
      });
    },
    //退出登录
    outlogin() {
      Dialog.confirm({
        title: "提示",
        message: "是否确认退出登录？",
      })
        .then(() => {
          // on confirm
          console.log(`退出登录`);
          localStorage.removeItem("userid");
          localStorage.removeItem("isvip");
          this.$router.push("/Login");
        })
        .catch(() => {
          // on cancel
        });
    },
    //VIP套餐被选中回调 index, 选中的套餐索引
    onChange(index) {
      this.chosenCoupon = index;
      console.log(`选中`, index);
      Notify({ type: "success", message: "支付成功" });
      Dialog.alert({
        title: "恭喜你成为VIP客户！",
        message: "Congratulations on becoming a VIP customer！",
        theme: "round-button",
      }).then(() => {
        // on close
        this.showList = false;
        localStorage.setItem("isvip", "true");
        this.isvip = "true";
      });
    },
    //点击右上角兑换VIP回调
    onExchange(code) {
      console.log(`兑换码`, code);
    },
  },
};
</script>

<style scoped>
.formls {
  padding: 10px;
}

.sadasf {
  padding-bottom: 100px;
}

.sanscy {
  font-size: 13px;
  margin: 10px;
}

.jaint {
  float: right;
  margin: 2px;
}

.stis {
  font-size: 15px;
  margin: 10px;
  padding: 5px;
}

.stisju {
  width: 100%;
  height: 130px;
  margin-top: 100px;
}

.stjs {
  box-shadow: #bababa 1px 1px 3px;
  border-radius: 10px;
  background-color: #ffffff;
  margin: 20px;
}

.jbsdht {
  position: absolute;
  width: 100%;
  margin-top: 25px;
}

.asldaji {
  color: #fefefe;
  font-size: 9px;
  font-weight: 900;
  float: right;
  margin: 5px;
}

.jisajdmx {
  color: #ffffff;
  margin-left: 10px;
}

.bianho {
  color: #ffffff;
  margin: 10px;
  margin-top: 40px;
  font-size: 10px;
  font-weight: 900;
}

.ann {
  float: right;
  margin-top: 50px;
  margin-right: 5px;
}

.sade {
  padding: 5px;
  display: flex;
  align-items: center;
}

.sadaxs {
  color: #ffffff;
  margin-left: 5px;
}

.dbsab {
  height: 100px;
  width: 100%;
  background: linear-gradient(to bottom, #0a0a0a, #ffffff);
  border: none;
}

.kapian {
  position: absolute;
  background-color: rgb(0, 0, 0);
  box-shadow: #696060 1px 10px 3px;
  width: 90%;
  height: 150px;
  margin: 17px;
  margin-top: 30px;
  border-radius: 10px;
}
</style>
