          <template #button>
            <van-button size="small">发送验证码</van-button>
          </template>
        </van-field>
      </div>

      <div style="margin: 16px" v-show="showlogin == 2">
        <van-button
          style="background: linear-gradient(to right, #3fac33, #1cb816); border: none"
