          block
          hairline
          type="info"
          @click="longdeji()"
          >微信一键授权登录</van-button
        >
      </div>

      <div style="margin: 16px" v-show="showlogin == 2">
        <van-button round block plain hairline type="info" @click="qtsjh()"
