    //点击本机一键授权
    longdeji() {
      this.showlogin = 2;
      console.log(`本机一键授权`);
      Notify({ type: "warning", message: "请使用手机号登录" });
    },
    //点击其他手机号登录
    qtsjh() {
      this.showlogin = 1;
      console.log(`其他手机号登录`);
    },
    //点击登录
    LoGIN() {
      console.log(`登录`);
      //存储用户信息
      localStorage.setItem("userid", "1");
      localStorage.setItem("isvip", "false");
      this.$router.push("/vip");
    },
  },
};
</script>

