import Vue from 'vue'
import VueRouter from 'vue-router'

import BOX from '@/views/BOX.vue'
import index from '@/views/index.vue'
import community from '@/views/community.vue'
import QRcode from '@/views/QRcode.vue'
import store from '@/views/store.vue'
import vip from '@/views/vip.vue'
import zhaojiaolian from '@/components/zhaojiaolian.vue'
import zhaokechneg from '@/components/zhaokechneg.vue'
import Login from '@/views/Login.vue'
import JRWM from '@/components/JRWM.vue'
import DJ from '@/components/DJ.vue'
import qickep from '@/components/qickep.vue'
import guaniess from '@/components/guaniess.vue'
import diusdji from '@/components/diusdji.vue'
import cztx from '@/components/cztx.vue'
import tk from '@/components/tk.vue'
import sj from '@/components/sj.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'BOX',
    component: BOX,
    meta: {
      title: "上海智慧健身"
    },
    children: [
      {
        path: '/qickep',
        name: 'qickep',
        component: qickep,
        meta: {
          title: "器材科普"
        }
      },
      {
        path: '/guaniess',
        name: 'guaniess',
        component: guaniess,
        meta: {
          title: "馆内设施"
        }
      },
      {
        path: '/diusdji',
        name: 'diusdji',
        component: diusdji,
        meta: {
          title: "丢失登记"
        }
      },
      {
        path: '/DJ',
        name: 'DJ',
        component: DJ,
        meta: {
          title: "点歌中心"
        }
      },
      {
        path: '/index',
        name: 'index',
        component: index,
        meta: {
          title: "上海智慧健身"
        },
        redirect: "/zhaojiaolian",//子路由容器默认显示该页面
        children: [
          {
            path: '/zhaojiaolian',
            name: 'zhaojiaolian',
            component: zhaojiaolian,
            meta: {
              title: "找教练"
            }
          },
          {
            path: '/zhaokechneg',
            name: 'zhaokechneg',
            component: zhaokechneg,
            meta: {
              title: "找课程"
            }
          }, {
            path: '/tk',
            name: 'tk',
            component: tk,
            meta: {
              title: "团课"
            }
          }
          , {
            path: '/sj',
            name: 'sj',
            component: sj,
            meta: {
              title: "私教"
            }
          }
        ]
      },
      {
        path: '/community',
        name: 'community',
        component: community,
        meta: {
          title: "健友圈"
        }
      },
      {
        path: '/QRcode',
        name: 'QRcode',
        component: QRcode,
        meta: {
          title: "健身码"
        }
      },
      {
        path: '/store',
        name: 'store',
        component: store,
        meta: {
          title: "商城"
        }
      },
      {
        path: '/vip',
        name: 'vip',
        component: vip,
        meta: {
          title: "我的VIP"
        }
      }
    ]
  },
  {
    path: '/Login',
    name: 'Login',
    component: Login,
    meta: {
      title: "登录-开启智慧健身之旅"
    }
  },
  {
    path: '/JRWM',
    name: 'JRWM',
    component: JRWM,
    meta: {
      title: "鑫圣运动合伙人招募"
    }
  }, {
    path: '/cztx',
    name: 'cztx',
    component: cztx,
    meta: {
      title: "成长体系"
    }
  }

]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

//路由守卫
router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  console.log(`路由：`, to.path);
  // if (to.path === '/') {
  //   next('/index') // 如果当前要导航到'/'，则重定向到'/index'
  // } else {
  //   next() // 确保一定要调用 next() 方法，否则路由不会发生变化
  // }
  next()
})


export default router
