<template>
  <div>
    <div class="xfai">
      <img src="@/assets/img/kj.gif" alt="" srcset="" @click="aimy" />
    </div>

    <!-- 容器 融入底部导航 -->
    <router-view />
    <van-tabbar route>
      <van-tabbar-item replace to="/index">
        首页
        <template #icon>
          <van-icon name="home-o" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item replace to="/community">
        社区
        <template #icon>
          <van-icon name="link-o" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item replace to="/QRcode">
        健身码
        <template #icon>
          <van-icon name="qr" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item replace to="/store">
        商城
        <template #icon>
          <van-icon name="shop-collect-o" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item replace to="/vip"
        >我的
        <template #icon>
          <van-icon name="vip-card-o" />
        </template>
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  name: "LufsVueBOX",

  data() {
    return {};
  },

  mounted() {
    var userid = localStorage.getItem("userid");
    console.log(`当前登录用户`, userid);
    if (userid === null) {
      this.$router.push("/Login");
    }
    if (this.$route.path === "/") {
      this.$router.replace("/index");
    }
  },

  methods: {
    aimy() {
      Toast("你的IP形象正在训练");
    },
  },
};
</script>

<style scoped>
.xfai {
  z-index: 999;
  position: fixed;
  margin-top: 100%;
  margin-left: 80%;
  width: 50px;
  height: 50px;
  background-color: rgb(76, 186, 255);
  border-radius: 50%;
}
.xfai > img {
  width: 100%;
  border-radius: 50%;
}
</style>
